@import url("https://fonts.googleapis.com/css2?family=Inter&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Ubuntu&display=swap");
@import url("https://fonts.googleapis.com/css2?family=JetBrains+Mono&display=swap");
/* Font */
.single-blog-wrapper * {
  margin: revert;
  padding: revert;
  list-style-type: revert;
  line-height: 1.5rem;
}
.single-blog-wrapper code, .single-blog-wrapper pre {
  border-radius: 5px;
  background-color: #282c34;
  color: #abb2bf;
}
.single-blog-wrapper code {
  padding: 0.2em 0.4em;
  line-break: anywhere;
  display: inline-block;
  line-height: 1.35;
  font-family: "JetBrains Mono", monospace;
  font-size: 13px;
}
.single-blog-wrapper code span {
  display: contents;
}
.single-blog-wrapper pre {
  border: 1px solid #CCCCCC;
  overflow: auto;
  padding: 6px 10px;
}
.single-blog-wrapper pre code {
  font-size: 14px;
}
.single-blog-wrapper blockquote {
  margin: 0;
  border-left: 5px solid #7a7a7a;
  font-style: italic;
  padding: 1.33em;
  text-align: left;
}
.single-blog-wrapper table {
  padding: 1rem 0;
}
.single-blog-wrapper th, .single-blog-wrapper td {
  padding: 6px 13px;
  border: 1px solid black;
}
.single-blog-wrapper hr {
  background: black;
}
.single-blog-wrapper ul, .single-blog-wrapper ol {
  padding-left: 20px;
}

.single-blog-wrapper {
  font-family: "Ubuntu", system-ui, sans-serif;
}

